.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #000; /* خلفية سوداء إذا كان الفيديو أصغر */
}

.App-header {
  width: 100%;
  max-width: 800px; /* أقصى عرض للفيديو على الشاشات الكبيرة */
  padding: 10px;
}

.App-video {
  width: 100%; /* يجعل الفيديو يستجيب لحجم الشاشة */
  height: auto; /* يحافظ على نسبة العرض/الارتفاع */
  border-radius: 12px; /* حواف مستديرة لجمالية */
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
}
